var timeOut; //定时器

import api from '../../api/api.js'
export default {
	data() {
		return {
			currFocus: "",
			index: -1,
			classifies: [{
					name: "Android",
					category: "Android"
				},
				{
					name: "iOS",
					category: "iOS"
				},
				{
					name: "App",
					category: "App"
				},
				{
					name: "前端",
					category: "前端"
				},
				{
					name: "瞎推荐",
					category: "瞎推荐"
				},
				{
					name: "拓展资源",
					category: "拓展资源"
				},
				{
					name: "休息视频",
					category: "休息视频"
				}
			],
			params: {
				url: "",
				desc: "",
				who: "",
				type: "",
				debug: true
			}
		}
	},
	methods: {
		//picker选择回调
		onPickerChanged(event) {
			this.index = event.detail.value;
		},
		//拿到input输入内容
		onInput(event) {
			let key = event.currentTarget.dataset.key;
			let value = event.detail.value;
			this.params[key] = value;
		},
		//提交表单
		onSubmit(event) {
			// 非空校验
			if (!this.params.url) {
				this.currFocus = "url";
				uni.showToast({
					icon: "none",
					title: "请输入干货的网址"
				})
				return;
			}
			if (!this.params.desc) {
				this.currFocus = "desc";
				uni.showToast({
					icon: "none",
					title: "请输入干货描述"
				})
				return;
			}
			if (!this.params.who) {
				this.currFocus = "who";
				uni.showToast({
					icon: "none",
					title: "请输入干货的署名"
				})
				return;
			}
			if (this.index == -1) {
				uni.showToast({
					icon: "none",
					title: "请选择干货类型"
				})
				return;
			}
			this.params.type = this.classifies[this.index].category;
			uni.showLoading({
				title: "提交中..."
			})
			console.log(this.params)
			api.post("/add2gank", this.params)
				.then(res => {
					console.log("res:"+res);
					uni.hideLoading();
					uni.showToast({
						icon: "success",
						title: "提交成功！"
					})
					timeOut = setTimeout(function() {
						// 提交成功后返回上页
						wx.navigateBack({
							delta: 1
						})
					}, 1000);
				}).catch(e => {
					uni.hideLoading();
					uni.showToast({
						icon: "none",
						title: e
					})
				})
		},
		onUnload(){
			clearTimeout(timeOut);
		}
	}
}
